import uniSwiperDot from '@/components/uniSwiperDot/uni-swiper-dot.vue';
export default {
	components: { uniSwiperDot },
	data() {
		return {
			info: [
				{
					url: '/static/swiper/1.jpg'
				},
				{
					url: '/static/swiper/2.jpg'
				},
				{
					url: '/static/swiper/3.jpg'
				}
			],
			current: 0,
			mode: 'indexes',
			dotsStyles: {},
			contentData: ['所在楼层', '总楼层', '使用面积', '朝向', '装修情况', '出租方式'],
			contentDatas: ['18层', '26层', '10㎡', '南', '精装', '短租'],
			configuration: '空调、冰箱、沙发、床、微波炉空调、冰箱、沙发、床、微波炉',
			pretend: [
				{
					url: '/static/other/pretendfri_boy_light.png',
					name: '张三'
				},
				{
					url: '/static/other/pretendfri_boy_light.png',
					name: '李四'
				},
				{
					url: '/static/other/pretendfri_girl_light.png',
					name: '张三'
				},
				{
					url: '/static/other/pretendfri_boy_light.png',
					name: '火山'
				},
				{
					url: '/static/other/pretendfri_girl_light.png',
					name: '李武'
				},
				{
					url: '/static/other/pretendfri_boy_light.png',
					name: '骑士'
				}
			]
		};
	},
	onLoad() {},
	methods: {
		change(e) {
			this.current = e.detail.current;
		},
		map(){
			uni.navigateTo({
					url: '/pages/detail/map',
					success: res => {},
					fail: () => {},
					complete: () => {}
				})
		}
	}
};
